const menus = [
  {
    icon: '',
    span: '我的咨询',
  }, {
    icon: '',
    url: '/pages/report/index',
    span: '我的报告',
  }, {
    icon: '',
    span: '我的方案',
  }, {
    icon: '',
    span: '我的打卡',
  }, {
    icon: '',
    span: '我的精品课',
  }, {
    icon: '',
    span: '关于米茶',
  },
];
export default {
  data() {
    return {
      menus,
    }
  },
  methods: {
    handleItemClick(index) {
      const target = this.menus[index];

      if (!target.url) {
        return;
      }

      uni.navigateTo({ url: target.url });
    }
  },
}
